/************** App.js *****************/
.anchor{
  display: block;
  height: 500; /*same height as header*/
  margin-top: -0px; /*same height as header*/
  visibility: hidden;
}

.headshot-img{
  border:1px solid darkslateblue;
}

.link-button{
  font-size: 16px;
  font-weight: 600;
  text-transform: uppercase;
  color: darkslateblue;
  display: inline-block;
  position: relative;
  margin-right: 20px;
  transition: all 0.2s ease 0s
}

.link-button:before {
  content: '';
  position: absolute;
  left: 0px;
  bottom: -2px;
  width: 50px;
  height: 2px;
  background: linear-gradient(18deg, sienna, rgba(255, 0, 0, 0) 97.71%);
  transition: all 0.4s ease 0s
}

.link-button:hover {
  color: sienna;
  font-weight: 800;
  font-size: 16px;
  text-decoration: none;
}

/************** card formatting *****************/
.card-style{
  border-color: darkslateblue;
  border-width: thin;
  border-radius: 5px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
